import React, { useEffect, useState } from "react";
import initScripts from "../../assets/js/scripts";
import initCustom from "../../assets/js/custom";
import { Accordion } from "react-bootstrap";
import { Button } from "@mui/material";
import { Form, Link } from "react-router-dom";
import { ClassNames } from "@emotion/react";
// import About from "./../About";

function PassengerInfo() {
  // todo:     
  let [bagCount2, setBagCount2] = useState<number>(0);
  

  const [array, setArray] = useState<Array<string>>(["a"]);

  useEffect(() => {
    initScripts();
    initCustom();
  }, []);

  // todo :  
  const onClickAdd = (idx: number) => {
    setArray([...array, "a"]);
  };
  const onClickRemove = (idx: number) => {
    if (array.length > 1) {
      setArray((prevArray) => prevArray.filter((_, i) => i !== idx));
    }
  };

  //  todo:  

  const increaseCount2 = () => {
    if (bagCount2 < 2) {
      // bagCount1 1 .
      const updatedCount2 = bagCount2 + 1;

      setBagCount2(updatedCount2);
    } else {
      //  2    .
      alert("1  2 ");
    }
  };


  //  todo:  
  const decreaseCount2 = () => {
     if (bagCount2 > 0) {
      bagCount2 -= 1;
      setBagCount2(bagCount2); //   
    }
  };

  

  return (
    <>
      <div className="hero hero-inner">
        <div className="container">
          <div className="row align-items-center">
            <div className="col-lg-12">
              <div className="intro-wrap">
                <h1 className="mb-5">
                  <span className="d-block text-center"></span>
                  <h3 className="mb-5" color="white">
                    
                  </h3>
                </h1>
              </div>
            </div>
          </div>
        </div>
      </div>

      <Accordion className="testtttttttt1" defaultActiveKey={["0"]} alwaysOpen>
        <Accordion.Item eventKey="0">
          <Accordion.Header> #1</Accordion.Header>
          <Accordion.Body>
          Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
          </Accordion.Body>
        </Accordion.Item>

        {/*     */}
        <Accordion.Item eventKey="1">
          <Accordion.Header> () #3</Accordion.Header>
          <Accordion.Body>
            <div className="bagage">
              <p>
                  :  1 (5kg) +  1(20kg)  
                  :  20kg
              </p>
              <p>  1 2  </p>
              <table className="table">
                <thead className="test1">
                  <tr>
                    <th scope="col"></th>
                    <th scope="col"> </th>
                    <th scope="col"></th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td className="passengername"></td>
                    <td className="bagcount">
                      <div
                        className="btn-group-col"
                        role="group"
                        aria-label="Basic outlined example"
                      >
                        <button
                          type="button"
                          className="btn btn-outline-secondary opacity-50"
                          onClick={decreaseCount2}
                        >
                          -
                        </button>

                        <button
                          type="button"
                          className="btn btn-outline-dark"
                          disabled
                        >
                          {bagCount2}
                        </button>

                        <button
                          type="button"
                          className="btn btn-outline-secondary opacity-50"
                          onClick={increaseCount2}
                        >
                          +
                        </button>
                      </div>
                    </td>
                    <td className="bagprice"></td>
                  </tr>
                 
                  <tr>
                    <td></td>
                    <td>
                      <strong></strong>
                    </td>
                    <td>
                      <strong></strong>
                    </td>
                  </tr>
                </tbody>
              </table>
              <div className="toboardingpass">
                <Link to={"/boardingpass"}>
                  <button type="button" className="btn btn-outline-dark">
                    
                  </button>
                </Link>
              </div>
            </div>
          </Accordion.Body>
        </Accordion.Item>
        {/*     */}
      </Accordion>
    </>
  );
}

export default PassengerInfo;
