import React, { useEffect, useState } from "react";
import WriteNoticeService from '../../../../services/notice/WriteNoticeService';
import { Pagination } from "@mui/material";
import { Link } from "react-router-dom";
import IWriteNotice from "../../../../types/writeNotice/IWriteNotice";

function AdminNotice() {

  // todo:  
//   
const [notice, setNotice] = useState<Array<IWriteNotice>>([]);
//  
const [searchTitle, setSearchTitle] = useState<string>("");

// todo:   : page(), count(), pageSize(3,6,9 )
const [page, setPage] = useState<number>(1);
const [count, setCount] = useState<number>(1);
const [pageSize, setPageSize] = useState<number>(6); // 1
// todo:  pageSizes :  (  )
const pageSizes = [3, 6, 9];

// todo:  
useEffect(() => {
  retrieveNotice(); //  
}, [page, pageSize]);

//    
const retrieveNotice = () => {
  WriteNoticeService.getAdminNoticeAll(searchTitle, page -1, pageSize) //  
  .then((response: any)=>{
    const { notice, totalPages } = response.data;
    setNotice(notice);
    setCount(totalPages);
    console.log("response", response.data);
  })
  .catch((e: Error)=>{
    //   
    console.log(e);
  })
};

//     
const onChangeSearchTitle = (e: any) => {
  const searchTitle = e.target.value;
  setSearchTitle(searchTitle);
};


//  todo: Pagination  ()
//     => page   
const handlePageChange = (event:any, value:number) => { 
    // value ==  
    setPage(value);
 }

return (
  <>
      {/* dname start */}
    <div className="row mb-5 justify-content-center">
      {/* w-50 :  , mx-auto : (margin: 0 auto), justify-content-center */}
      <div className="col-12 w-50 input-group mb-3">
        <input
          type="text"
          className="adminForm"
          placeholder=" "
          value={searchTitle}
          onChange={onChangeSearchTitle}
        />
        <div className="input-group-append">
          <button
            className="adminNoticeBtn"
            type="button"
            onClick={retrieveNotice}
          >
            Search
          </button>
        </div>
      </div>
    </div>
    {/* dname end */}

    

    {/* table start */}
    <div className="col-md-12">
      {/* table start */}
      <table className="table">
        <thead className="table-light">
          <tr>
            <th scope="col" id="adminTh1"></th>
            <th scope="col" id="adminTh2"></th>
            <th scope="col" id="adminTh3"> </th>
            <th scope="col" id="adminTh4"> / </th>
          </tr>
        </thead>
        <tbody>
          {notice &&
            notice.map((data) => (
              <tr key={data.noticeId}>
                <td id="adminTd1"><a className="adminTdA">{data.noticeTitle}</a></td>
                <td id="adminTd2">{data.memberName}</td>
                <td id="adminTd3">{data.insertTime}</td>
                <td id="adminTd4">
                  <Link to={"/admin/notice/" + data.noticeId}>
                    <span className="badge bg-success">Edit</span>
                  </Link>
                </td>
              </tr>
            ))}
        </tbody>
      </table>
      {/* table end */}
    </div>
    {/* table end */}

    {/* paging  */}
    <div className="adminNoticePageBtn">
      

      {/*  : count={1} , page={} */}
      <Pagination
        className="my-3"
        count={count}
        page={page}
        siblingCount={1}
        boundaryCount={1}
        variant="outlined"
        shape="rounded"
        onChange={handlePageChange}
      />
    </div>
    {/* paging  */}
  </>
)
}

export default AdminNotice;
