import { useEffect, useRef, useState } from "react" 
import {
  PaymentWidgetInstance,
  loadPaymentWidget,
  ANONYMOUS,
} from "@tosspayments/payment-widget-sdk" 
import { Modal } from "react-bootstrap";
import AuthService from "../../services/auth/authService";
import { start } from "repl";
import { useSelector } from "react-redux";
import { RootState } from "../../store/store";
import { useNavigate } from "react-router-dom";

const clientKey = "test_gck_docs_Ovk5rk1EwkEbP0W43n07xlzm" 
const customerKey = "Rkhxv1jRmshLqgQjm3gY4" 


function BaggagePaymentModal(props: any) {

  

    const paymentWidgetRef = useRef<PaymentWidgetInstance | null>(null) 
    const paymentMethodsWidgetRef = useRef<ReturnType<
      PaymentWidgetInstance["renderPaymentMethods"]
    > | null>(null) 
    
    const [price, setPrice] = useState(0) ;
    const [originPrice, setOriginPrice] = useState(0);
    const { user: currentUser } = useSelector((state:RootState)=> state.auth);
    const [mile, setMile] = useState(0);
    const navi = useNavigate();
    const [orderId, setOrderId] = useState("");

    useEffect(()=>{
      
      if (props.price) {
        setPrice(props.price);
        setOrderId(props.bagNumber)
      }
    },[props])
  
    useEffect(() => {
      
      // alert(startReserveNum+"\n"+finalReserveNum)
      // ReservationService



      (async () => {
        // ------    ------
        //   customerKey  ANONYMOUS .
        const paymentWidget = await loadPaymentWidget(clientKey, customerKey)  //  
        // const paymentWidget = await loadPaymentWidget(clientKey, ANONYMOUS)  //  
        // ------   UI  ------
        //  UI   . `#payment-method`  CSS     .
        // DOM     .
        // https://docs.tosspayments.com/reference/widget-sdk#renderpaymentmethods---
        const paymentMethodsWidget = paymentWidget.renderPaymentMethods(
          "#payment-widget",
          { value: price },
          //    UI variantKey
          //  variantKey    .  UI     .
          // https://docs.tosspayments.com/guides/payment-widget/admin#--ui
          { variantKey: "DEFAULT" }
        ) 
        // ------   UI  ------
        //  UI   . `#agreement`  CSS  .
        // https://docs.tosspayments.com/reference/widget-sdk#renderagreement-
        paymentWidget.renderAgreement(
          '#agreement',
          { variantKey: "AGREEMENT" } //   UI 
        )
        paymentWidgetRef.current = paymentWidget 
        paymentMethodsWidgetRef.current = paymentMethodsWidget 
      })() 
      if (currentUser)
      if (currentUser.memberId)
      AuthService.getUserInfo(currentUser.memberId)
      .then((response)=>{console.log(response)
      setMile(response.data.memberMile)
      })
      .catch((e:Error)=>{console.log(e)})

    }, [props, price]) 
  
    useEffect(() => {
      const paymentMethodsWidget = paymentMethodsWidgetRef.current 
  
      if (paymentMethodsWidget == null) {
        return 
      }
  
      // ------   ------
      //    .
      // https://docs.tosspayments.com/reference/widget-sdk#updateamount-
      paymentMethodsWidget.updateAmount(
        price
      ) 
    }, [price]) 

  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
      </Modal.Header>
      <Modal.Body>
      <div>
      <h1 ></h1>
      <span >{`${price}`}</span><br/>
        
      <div >
      </div>
      {price > 0 &&
      <>
      {console.log(price)}
      <div id="payment-widget" />
      <div id="agreement" />
      </>
      }
      <button
        onClick={async () => {

          const paymentWidget = paymentWidgetRef.current 
          try {
            await paymentWidget?.requestPayment({
              orderId: `${orderId}`,
              orderName: " ",
              successUrl: `${window.location.origin}/baggage/payment/success`,
              failUrl: `${window.location.origin}/baggage/payment/fail`,
            }) 
          } catch (error) {
            //  
            alert(error);
            console.error(error) 
          }
        }}
      >
        
      </button>
    </div>
      </Modal.Body>
      <Modal.Footer>
      </Modal.Footer>
    </Modal>
  );
}

export default BaggagePaymentModal;
