//  

import { useEffect, useState } from "react";
import { Pagination } from "@mui/material";
import IWriteNotice from "../../types/writeNotice/IWriteNotice";
import NoticeService from "../../services/notice/NoticeService";

function NoticeList() {
  //  
  // customer  
  const [notice, setNotice] = useState<Array<IWriteNotice>>([]);
  // select      :  (question)
  const [searchSelect, setSearchSelect] = useState<string>("tltle");
  // (input) 
  const [searchKeyword, setSearchKeyword] = useState<string>("");

  // todo:    4
  // todo:   : page(), count(), pageSize(3,6,9 )
  const [page, setPage] = useState<number>(1);
  const [count, setCount] = useState<number>(1);
  const [size, setSize] = useState<number>(10); // 1

  // todo:  
  useEffect(() => {
    retrieveNotice(); //  
  }, [page]);

  // 
  const retrieveNotice = () => {
    //    : + (page), 1(pageSize)
    NoticeService.getAllNotice(searchSelect, searchKeyword, page - 1, size) //  
      .then((response: any) => {
        const { notice, totalPages } = response.data;
        setNotice(notice);
        setCount(totalPages);
        //  
        console.log("response", response.data);
      })
      .catch((e: Error) => {
        console.log(e);
      });
  };

  //   select  
  const onChangeSearchSelect = (e: any) => {
    setSearchSelect(e.target.value); //  -> 
  };

  //   input  
  const onChangeSearchKeyword = (e: any) => {
    setSearchKeyword(e.target.value); //  -> 
  };

  //  todo: Pagination  ()
  const handlePageChange = (event: any, value: number) => {
    setPage(value);
  };
  
  return (
    <div>
      {/*    :   */}
      {/*    */}
      <div>
        <div className="userNoticeDiv1">
          <select
            className="userNoticeSelectBox"
            onChange={onChangeSearchSelect}
            value={searchSelect}
          >
            <option key=" + " value=" + ">
               + 
            </option>
            <option key="" value="">
              
            </option>
            <option key="" value="">
              
            </option>
          </select>

          <input
            type="text"
            className="userNoticeTextInput"
            placeholder=" ."
            value={searchKeyword}
            onChange={onChangeSearchKeyword}
          />
          <button
            className="userNoticeSearchBtn"
            type="button"
            onClick={retrieveNotice}
          >
            
          </button>
        </div>
      </div>

      {/*    */}

      {/*  */}
      <div className="container">
        <div>
          <table className="table">
            <thead>
              <tr className="jwNoticeTr">
                <th scope="col">No</th>
                <th scope="col"></th>
                <th scope="col"></th>
                <th scope="col"></th>
              </tr>
            </thead>
            <tbody>
              {notice &&
                notice.map((data) => (
                  <tr key={data.noticeId}>
                    <td className="jwNoticeNumber">{data.noticeId}</td>
                    <td className="jwNoticeTitle">
                      <a href={`/notice/${data.noticeId}`} className="jwNoticeA">
                        {data.noticeTitle}
                      </a>
                    </td>
                    <td className="jwNoticeTime">{data.insertTime.split(' ')[0]}</td>
                    <td className="jwNoticeWriter">{data.memberName}</td>
                  </tr>
                ))}
            </tbody>
          </table>

          {/* table end */}
        </div>

        {/*   */}
        <div className="noticePageBtn">
          <Pagination
            className="my-3"
            count={count}
            page={page}
            siblingCount={1}
            boundaryCount={1}
            variant="outlined"
            shape="rounded"
            onChange={handlePageChange}
          />
        </div>
        {/*   */}
      </div>
      {/* table end */}
    </div>
  );
}

export default NoticeList;
