import React from 'react'
import { useNavigate, useParams } from 'react-router-dom';
import IWriteNotice from '../../../../types/writeNotice/IWriteNotice';
import { useState } from 'react';
import NoticeService from '../../../../services/notice/NoticeService';
import { useEffect } from 'react';

function AdminNoticeFix() {

    // todo:  
    //    (noticeId)
    const { noticeId } = useParams();
    //   
    let navigate = useNavigate();
  
    //  ( :  )
    const initialNotice = {
        noticeId: 0,
        noticeTitle: "",
        noticeContent: "",
        noticeWriter: "",
        memberId: "",
        insertTime: "",
        updateTime: "",
        memberName: ""
    };
  
    // 
    const [notice, setNotice] = useState<IWriteNotice>(initialNotice);
    //      
    const [message, setMessage] = useState<string>("");
  
    // todo:  
    //  
    const getNotice = (noticeId: any) => {
      NoticeService.get(noticeId)         //   
        .then((response: any) => {
          setNotice(response.data);
          console.log(response.data);
        })
        .catch((e: Error) => {
          console.log(e);
        });
    };
  
    //     + noticeId   
    useEffect(() => {
      if (noticeId) getNotice(noticeId);
    }, [noticeId]);
  
    // input     
    const handleInputChangeTitle = (event: React.ChangeEvent<HTMLInputElement>) => {
      const { name, value } = event.target;
      setNotice({ ...notice, [name]: value });
    };

    // input     
    const handleInputChangeContent = (event: React.ChangeEvent<HTMLTextAreaElement>) => {
        const { name, value } = event.target;
        setNotice({ ...notice, [name]: value });
      };
    
  
    //  
    const updateNotice = () => {
      NoticeService.update(notice.noticeId, notice) //  
        .then((response: any) => {
          console.log(response.data);
          navigate("/admin")
          alert(" .");
        })
        .catch((e: Error) => {
          console.log(e);
        });
    };
  
    // 
    const deleteNotice = () => {
      NoticeService.remove(notice.noticeId) //  
        .then((response: any) => {
          console.log(response.data);
          alert(" .");
          //  
          navigate("/admin");
        })
        .catch((e: Error) => {
          console.log(e);
        });
    };
  
    return (
      <>
        <>
          {notice ? (
            <div className="col-6 mx-auto">
              <form>
                <div className="row g-3 align-items-center mb-3">
                  <div className="col-9">
                    <input
                      type="text"
                      id="noticeTitle"
                      className="adminNoticeFix1"
                      value={notice.noticeTitle}
                      onChange={handleInputChangeTitle}
                      placeholder=""
                      name="noticeTitle"
                    />
                  </div>
                </div>
  
                <div className="row g-3 align-items-center mb-3">
                  <div className="col-9">
                    <textarea
                      id="noticeContent"
                      className="adminNoticeFix2"
                      value={notice.noticeContent}
                      onChange={handleInputChangeContent}
                      placeholder=""
                      name="noticeContent"
                    />
                  </div>
                </div>
              </form>
  
              <div className="row g-3 mt-3 mb-3">
                <button
                  onClick={deleteNotice}
                  className="btn btn-outline-danger ms-3 col"
                >
                  Delete
                </button>
  
                <button
                  type="submit"
                  onClick={updateNotice}
                  className="btn btn-outline-success ms-2 col"
                >
                  Update
                </button>
              </div>
  
              {message && (
                <p className="alert alert-success mt-3 text-center">{message}</p>
              )}
            </div>
          ) : (
            <div className="col-6 mx-auto">
              <br />
              <p>Please click on a Dept...</p>
            </div>
          )}
        </>
      </>
    );
  }

export default AdminNoticeFix