// 1:1   

import React, { useEffect, useState } from "react";

import { Link, useNavigate } from "react-router-dom";
import IQboard from "../../../types/Center/IQboard";
import CustomerService from "../../../services/customer/CustomerService";

import { RootState } from "../../../store/store";
import { useSelector } from "react-redux";
import { Pagination } from "@mui/material";

function QuestionBoardPage() {

  const [question, setQuestion] = useState<Array<IQboard>>([]);

  const [title, setTitle] = useState<string>("");

  const navi = useNavigate();

  //    
  const { user: currentUser } = useSelector((state: RootState) => state.auth);

  // todo:    4
  // todo:   : page(), count(), pageSize(3,6,9 )
  const [page, setPage] = useState<number>(1);
  const [count, setCount] = useState<number>(1);
  const [size, setSize] = useState<number>(10); // 1

  //  
  //       + 
  useEffect(() => {
    retrieveQuestion(); //  
  }, [page]);

  // 
  const retrieveQuestion = () => {
    if (currentUser?.memberId != undefined && currentUser?.memberId != null)
    CustomerService.getAll(currentUser.memberId, page - 1, size)
      .then((response: any) => {
        const { question, totalPages } = response.data;
        setQuestion(question);
        setCount(totalPages);
        console.log("response", response.data);
      })
      .catch((e: Error) => {
        console.log(e);
      });
  };

  // 
  const searchTitleContaining = () => {
    if (currentUser?.memberId != undefined && currentUser?.memberId != null)
    CustomerService.getSearch(title, currentUser.memberId, page -1, size)
    .then((response: any) => {
      const { question, totalPages } = response.data;
        setQuestion(question);
        setCount(totalPages);
      console.log("response", response.data);
    })
    .catch((e: Error) => {
      console.log(e);
    });
  }

  useEffect(() => {
    if (!currentUser) navi("/login");
  }, [currentUser])
  

  
  //     
  const onChangeSearchTitle = (e: any) => {
    const title = e.target.value;
    setTitle(title);
  };

  const deleteQuestion = (titleId: number) => {
    // QuestionBoardService      .
    //    .
    //  QuestionBoardService    .
    CustomerService.remove(titleId)
      .then((response: any) => {
        console.log(response.data);
        //      
        retrieveQuestion();
      })
      .catch((e: Error) => {
        console.log(e);
      });
  };

  const handlePageChange = (event: any, value: number) => {
    setPage(value);
  };

  return (
    <div>
      {/*  */}
      <div className="hero hero-customer">
        <div className="container">
          <div className="row align-items-center">
            <div className="col-lg-6 mx-auto text-center">
              <div className="intro-wrap">
                <h1 className="mb-0">1:1 </h1>
                <p className="text-white">.</p>
              </div>
            </div>
          </div>
        </div>
      </div>
      {/*   */}

      <div>
        {/* w-50 :  , mx-auto : (margin: 0 auto), justify-content-center */}
        <div className="userQuestionDiv1">
          <input
            type="text"
            className="userQuestionInput"
            placeholder=""
            value={title}
            onChange={onChangeSearchTitle}
          />

          <button
            className="userQuestionSearchBtn"
            type="button"
            onClick={searchTitleContaining}
          >
            
          </button>
        </div>
      </div>

      {/*   */}
      <div className="untree_co-section">
        <div className="container">
          <div className="row">
            <div className="col-md-12">
              <div>
                <p className="answer-text-big">11 </p>
                <p className="answer-text-small"> / </p>
              </div>

              {/* table start() */}
              <table className="table">
                <thead>
                  <tr>
                    <th scope="col">No</th>
                    <th scope="col" className="userQuestionTd1"></th>
                    <th scope="col"> </th>
                    <th scope="col"> </th>
                    <th scope="col"></th>
                  </tr>
                </thead>
                <tbody>
                  {question &&
                    question.map((data) => (
                      <tr key={data.titleId}>
                        <td>{data.titleId}</td>
                        <td className="userQuestionTd1">
                          <a href={`/question-board/${data.titleId}`}>{data.title}</a>
                        </td>
                        <td>{data.insertTime.split(' ')[0]}</td>
                        {data.answerYn === "N" && currentUser?.memberAuth === "ROLE_ADMIN" ? <td><a href={`/question-board/${data.titleId}`}><button className="userQuestionDeleteBtn"></button></a></td>:<td>{data.answerYn}</td>}
                        <td>
                          <button
                            className="userQuestionDeleteBtn"
                            onClick={() => deleteQuestion(data.titleId)}
                          >
                            
                          </button>
                        </td>
                      </tr>
                    ))}
                </tbody>
              </table>
              {/*   */}
        <div className="noticePageBtn">
          <Pagination
            className="my-3"
            count={count}
            page={page}
            siblingCount={1}
            boundaryCount={1}
            variant="outlined"
            shape="rounded"
            onChange={handlePageChange}
          />
        </div>
        {/*   */}
              {/* table end */}
            </div>

            {/* table start() */}

            {/* table end */}

            <div className="container">
              <div className="answer-careful-notes">
                <p className="answer-text-middle">  </p>
                <ul className="answer-carful-list">
                  <li>
                    19       
                       .
                  </li>
                  <li>
                    [ ]     ,
                        .
                  </li>
                  <li>
                          
                           
                            
                     .
                  </li>
                  <li>
                    , , ,   ,   , 
                          
                            
                    .
                  </li>
                  <li>
                    , ,      
                         
                    .
                  </li>
                </ul>
              </div>
            </div>

            {/*     */}
            <div className="shortcuts-often-question">
                      .
              <a href={"/question"}>
                <button className="shortcuts-often-question-btn">
                     
                </button>
              </a>
            </div>

            {/*     */}

            <div className="button1">
              <div className="gray_box3">
                <Link
                  to="/addquestion-board"
                  className="btn btn-primary"
                  style={{
                    width: "100%",
                    padding: "10px",
                    borderRadius: "5px",
                  }}
                >
                  
                </Link>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default QuestionBoardPage;
