//QnaService.ts : axios   ( CRUD  )
// axios  :  

import IOperationinfo from "../../types/operationInfo/IOperationinfo";
import http from "../../utils/http-common";




//   + Like (paging  : page(), size(1))
//  : (question)  :  like 
//  : (questioner)  :  like 
//       : searchSelect (question,questioner)
//       : searchKeyword: 

const getAll = (
  StartAirport: string,
  FinalAirport: string,
  StartDate: string,
  OperationDate: string,
  page: number,
  pagesize: number
) => {
  return http.get<Array<IOperationinfo>>(
    `/tour/reserve?startAirport=${encodeURIComponent(
      StartAirport
    )}&page=${page}&pagesize=${pagesize}&finalAirport=${encodeURIComponent(
      FinalAirport
    )}&operationDate=${encodeURIComponent(
      OperationDate
    )}&sysdate=${encodeURIComponent(StartDate)}`
  );
};

//  
const get = (operationId: any) => {
  return http.get<IOperationinfo>(`/tour/reserve/${operationId}`);
};






const OperationService = {
  getAll,
  get
};

export default OperationService;
