// 1:1 

import React, { useState } from "react";

import { useNavigate } from "react-router-dom";
import CustomerService from "../../../services/customer/CustomerService";
import { RootState } from "../../../store/store";
import { useSelector } from "react-redux";
import { useEffect } from 'react';
import ICustomer from "../../../types/customer/ICustomer";


function AddboardList() {

  const navi = useNavigate();

  //    
  const { user: currentUser } = useSelector((state: RootState) => state.auth);

  const initialQuestion = {
    titleId: null,
    title: "",
    content: "",
    answerYn: "N",
    memberId: currentUser?.memberId,
    insertTime: "",
    parentBid: 0,
    answer: "",
    memberName: ""
  };



  const [question, setQuestion] = useState<ICustomer>(initialQuestion);


  useEffect(() => {
    if (!currentUser) navi("/login");
  }, [currentUser])

  //  
  const handleTitleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = event.target;
    setQuestion({ ...question, [name]: value });
  };

  //  
  const handleContentChange = (event: React.ChangeEvent<HTMLTextAreaElement>) => {
    const { name, value } = event.target;
    setQuestion({ ...question, [name]: value });
  };

  //  
  const saveQuestion = () => {
    var data = {
      title: question.title,
      content: question.content,
      memberId: question.memberId,
      insertTime: question.insertTime,
      answerYn: question.answerYn,
      answer: question.answer,
      parentBid: question.parentBid,
      titleId: null,
      memberName: null,
    };
    CustomerService.create(data) //  

      .then((response: any) => {
        console.log(response.data);
        navi(`/question-board`);
      })
      .catch((e: Error) => {
        console.log(e);
      });
  };

  

  const Onclinkev = () => {
    saveQuestion();
  };

  return (
    <>
      {/*  */}
      <div className="hero hero-customer">
        <div className="container">
          <div className="row align-items-center">
            <div className="col-lg-6 mx-auto text-center">
              <div className="intro-wrap">
                <h1 className="mb-0">1:1 </h1>
                <p className="text-white">.</p>
              </div>
            </div>
          </div>
        </div>
      </div>
      {/*  */}

      <div className="gray_box">
        {/*   */}
        <form className="k_box">
          <div className="form_group">
            <div className="k_title">
              <label className="add-answer-title-gap"></label>
              <input
                type="text"
                className="userQustionTitleInput"
                id="title"
                name="title"
                value={question.title}
                onChange={handleTitleChange}
                placeholder="  "
              />
            </div>
          </div>
          <div className="form-group">
            <label className="add-answer-content-gap"></label>
            <textarea
              className="userQuestionContentInput"
              name="content"
              value={question.content}
              onChange={handleContentChange}
              placeholder="  "
              style={{ height: "200px" }}
            />
          </div>
        </form>

        <div className="button1">
          <button
            type="button"
            className="btn btn-primary"
            onClick={Onclinkev}
            style={{ width: "10%", padding: "10px", borderRadius: "5px" }}
          >
            
          </button>
        </div>

        <div className="container">
          <div className="answer-careful-notes">
            <p className="answer-text-middle"> / </p>
            <ul className="answer-carful-list">
              <li>1.  / : 1:1 </li>
              <li>
                2.   
                <br />
                &nbsp;&nbsp; 1)  : , , , 
                <br />
                &nbsp;&nbsp; 2)   
                <br />
                &nbsp;&nbsp;&nbsp;-    : , ,  
                <br />
                &nbsp;&nbsp;&nbsp;-   : , , 
              </li>
              <li>3.      :   
                    ,   1:1  .
              </li>
            </ul>
          </div>
        </div>
      </div>
    </>
  );
}

export default AddboardList;
