import IPassport from "../../types/passport/IPassport";
import http from "../../utils/http-common";

//   + like (paging  : page(), size(1))
const getAll = (passportId : any) => {
  return http.get<Array<IPassport>>(`/tour/passport/${passportId}`);
};



// 
const create = (data:Array<IPassport>) => {
    return http.post<Array<IPassport>>("/tour/passport", data);

  };

  const PassportService = {
    getAll,
    create
  };

  export default PassportService;